.container{
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  -ms-flex-positive: 1;
      flex-grow: 1;
  width: 100%;
  height: 100%;
  background-image: url("./loginBg.jpg");
  -ms-flex-direction: column;
      flex-direction: column;
}

.spacebg{
  -ms-flex: 1 1;
      flex: 1 1;
}

.login{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex: 1 1;
      flex: 1 1;
}

.spacelg{
  -ms-flex: 2 1;
      flex: 2 1;
}

.boxlogin{
  display: -ms-flexbox;
  display: flex;
  min-width: 100px;
  height: 7cm;
  width: 8cm;
  background-color: white;
  border-radius: 10px;
  -ms-flex-direction: column;
      flex-direction: column;
}

.toplogin{
  height: 1.5cm;
  background-color: black;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
}

.bodylogin{
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 4 1;
      flex: 4 1;
}

.text{
  color: white;
  font-size: 120%;
}

.inputtext{
  margin-top: 0.8cm;
  margin-left: 1cm;
  margin-right: 1cm;
  height: 1cm;
  width: 6cm; 
  border-radius: 5px;
}

.button{
  margin-top: 0.5cm;
  height: 0.8cm;
  width: 2.5cm;
  border-radius: 5px;
}


